import "../css/loading.css";

const Loading = ({ data }) => {
  console.log("data : ", data);
  return (
    <div className="loading">
      {data === "loading" ? (
        <>
          <p>  .</p>
        </>
      ) : data === "done" ? (
        <>
          <p> .</p>
        </>
      ) : (
        <></>
      )}
    </div>
  );
};

export default Loading;
