import "../../css/modal.css";

const Modal = ({ data }) => {
  console.log("data : ", data);
  return (
    <div className="modal">
      {data === "success" ? (
        <>
          <img src="/images/icon_success.png" alt="" />
          <p>  .</p>
        </>
      ) : data === "fail" ? (
        <>
          <img src="/images/icon_fail.png" alt="" />
          <p>
               .
            <br />
                .
          </p>
        </>
      ) : (
        <>
          <img src="/images/icon_fail.png" alt="" />
          <p> .</p>
        </>
      )}
    </div>
  );
};

export default Modal;
